/*-------- Service --------*/

.service {
    padding-top: 129px;

    &__banner {
        color: #ffffff;
        background-color: #1d4693;
    }

    &__banner-columns {
        display: flex;    
        align-items: center;
        padding-top: 70px;
        padding-bottom: 70px;
    }

    &__banner-column--1 {
        width: 55%;
    }

    &__banner-column--2 {
        width: calc(45% - 56px);
        margin-left: 56px;
    }

    &__banner-title {
        font-family: $font-pf;
        font-size: 58px;
    }

    &__line-text {
        align-items: flex-start;
        margin-top: 50px;
    }

    &__tags {
        display: flex;
        align-items: center;
        margin-top: 85px;
    }

    &__tag:not(:first-child) {
        margin-left: 30px;
    }

    &__tag-image {
        width: 100%;
    }

    &__form {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        padding: 50px 36px;
        box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);

        background-color: #16336b;
        border-radius: 4px;
    }

    &__form-button {
        margin-top: 40px;
        margin-left: auto;
        margin-right: auto;
    }

    &__services-columns {
        display: flex;
        align-items: center;
    }

    &__services {
        padding-top: 117px;
        padding-bottom: 157px;
        position: relative;
    }

    &__services-wrapper {   
        position: relative;
        z-index: 2;
    }

    &__banner-wrapper {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
        height: 770px;
    }

    &__services-column--1 {
        width: 46%;
    }

    &__services-column--2 {
        width: 54%;
        padding-left: 49px;
        z-index: 1;
    }

    &__services-text {
        font-size: 19px;
        line-height: 1.6;
    }

    &__services-image {
        width: 100%;
    }

    &__services-buttons {
        display: flex;
        justify-content: center; 
        margin-top: 40px;
    }

    &__services-button {
        height: 52px;
        padding: 0 36px;
    }

    &__services-button:not(:first-child) {
        margin-left: 20px;
    }

    &__background {
        width: 100%;
        position: absolute;
        left: 0;
        bottom: 0;
    }

    &__cards-wrapper {
        margin-top: 150px;
        z-index: 2;
        position: relative;
    }

    &__services-cards {
        display: flex;
        flex-wrap: wrap;
        margin-top: 123px;
    }

    &__services-card {
        width: calc(33% - 16px);
        padding: 45px 40px;
        background-color: #ffffff;
        box-shadow: 1px 2px 9px 1px rgba(0, 1, 1, 0.22);
        border-radius: 5px;
        transition: background-color 0.2s ease-in-out;
    }

    &__services-card:hover {
        background-color: #1d4693;
        color: #ffffff;
    }

    &__services-card:not(:nth-child(3n+1)) {
        margin-left: 30px;
    }

    &__services-card:nth-child(n+4) {
        margin-top: 30px;
    }

    &__services-card-title {
        display: flex;
        align-items: center;
        
        min-height: 70px;
        margin-top: 35px;

        font-family: $font-sb;
        font-size: 27px;
        line-height: 1.3;
        transition: color 0.2s ease-in-out;
    }

    &__services-card-text {
        margin-top: 30px;
        font-size: 19px;
        line-height: 1.5;
        transition: color 0.2s ease-in-out;
    }

    &__content {
        position: relative;
    }

    &__content-wrapper {
        padding-top: 80px;
        z-index: 1;
        position: relative;
    }

    &__content-image {
        float: right;
        padding-left: 50px;
    }

    &__content-title {
        font-family: $font-pf;
        font-size: 64px;
    }

    &__content-subtitle {
        margin-top: 70px;
        font-family: $font-m;
        font-size: 22px;
        color: #1d4693;
    }

    &__content-text {
        margin-top: 60px;
        font-size: 19px;
        line-height: 1.5;
    }

    &__content-text--strong {
        display: block;
        font-family: $font-sb;
        font-size: 27px;
    }
}

@media (max-width: 1219px) {

    .service {

        &__services-card:not(:nth-child(3n+1)) {
            margin-left: 29px;
        }

        &__content-image {
            padding-left: 0px;
            width: 38%;
        }
    }
}

@media (max-width: 1099px) {

    .service {
        padding-top: 90px;

        &__tag:not(:first-child) {
            margin-left: 20px;
        }

        &__tags {
            margin-top: 45px;
        }

        &__line-text {
            margin-top: 40px;
            font-size: 24px;
        }
        
        &__banner-title {
            font-size: 45px;
        }  

        &__banner-column {
            width: 50%;
        }

        &__banner-column--2 {
            width: calc(50% - 30px);
            margin-left: 30px;
        }

        &__cards-wrapper {
            margin-top: 80px;
        }

        &__services-cards {
            margin-top: 80px;
        }

        &__services-card {
            padding: 30px;
        }

        &__services-card:not(:nth-child(3n+1)) {
            margin-left: 28px;
        } 
    }
}

@media (max-width: 989px) {

    .service {

        &__banner-wrapper {
            height: 600px;
        }

        &__banner-title {
            font-size: 38px;
        }

        &__line-text {
            margin-top: 30px;
            font-size: 19px;
        }

        &__tags {
            margin-top: 30px;
        }

        &__form-button {
            margin-top: 20px;
        }

        &__form {
            padding: 30px;
        }

        &__services {
            padding-top: 80px;
            padding-bottom: 80px;
        }

        &__services-text {
            font-size: 15px;
            line-height: 1.4;
        }

        &__services-column--2 {
            padding-left: 20px;
        }

        &__services-button {
            height: 40px;
            padding: 0 20px;
            font-size: 13px;
        }

        &__services-buttons {
            margin-top: 30px;
        }

        &__services-card {
            width: calc(33% - 8px);
            padding: 20px;
        }

        &__services-card:not(:nth-child(3n+1)) {
            margin-left: 15px;
        }

        &__services-card:nth-child(n+4) {
            margin-top: 15px;
        }

        &__services-card-title {
            min-height: 58px;
            margin-top: 20px;
            font-size: 20px;
        }

        &__services-card-text {
            margin-top: 20px;
            font-size: 15px;
        }

        &__services-card-image {
            width: 80px;
            height: 80px;
        }

        &__services-cards {
            margin-top: 40px;
        }

        &__content-title {
            font-size: 48px;
        }

        &__content-text {
            margin-top: 50px;
            font-size: 16px;
        }

        &__content-text--strong {
            font-size: 20px;
        }

        &__content-subtitle {
            margin-top: 50px;
            font-size: 18px;
        }
    }
}

@media (max-width: 767px) {

    .service {
        
        &__banner-wrapper {
            height: unset;
            padding-top: 40px;
            padding-bottom: 40px;
        }

        &__banner-column {
            width: 100%;
        }

        &__line-text--banner {
            font-size: 25px;
            align-items: center;
        }

        &__banner-title {
            font-size: 46px;
            text-align: center;
        }

        &__banner-column--2 {
            margin-left: 0;
            margin-top: 30px;
        }

        &__tags {
            order: 1;
        }

        &__services {
            padding-top: 40px;
        }

        &__services-columns {
            flex-direction: column;
        }

        &__services-column {
            width: 100%;
        }

        &__services-column--2 {
            padding-left: 0;
            margin-top: 30px;
        }

        &__services-cards {
            justify-content: space-between;
        }

        &__services-card {
            width: calc(50% - 8px);
        }

        &__services-card:nth-child(n) {
            margin-left: 0;
        }

        &__services-card:nth-child(n+3) {
            margin-top: 16px;
        }

        &__content-wrapper {
            padding-top: 50px;
        }

        &__content-image {
            width: 30%;
        }

        &__content-title {
            font-size: 34px;
        }

        &__line-text--content {
            margin-top: 20px;
            font-size: 17px;
        }

        &__content-text {
            margin-top: 40px;
            font-size: 15px;
        }

        &__content-text--strong {
            font-size: 15px;
        }

        &__content-subtitle {
            margin-top: 40px;
            font-size: 17px;
        }
    }
}

@media (max-width: 767px) and (min-width: 620px) {

    .service {

        &__services-card-wrapper {
            display: flex;
            align-items: center;
        }

        &__services-card-title {
            min-height: 45px;
            margin-top: 0;
            padding-left: 15px;
            font-size: 17px;
        }

        &__services-card-image {
            width: 65px;
            height: 65px;
        }
    }
}

@media (max-width: 619px) {

    .service {

        &__banner-title {
            font-size: 44px;
        }

        &__line-text--banner {
            font-size: 22px;
        }

        &__services-card-title {
            min-height: 44px;
            font-size: 18px;
        }

        &__services-card-text {
            font-size: 14px;
        }

        &__content-wrapper {
            display: flex;
            flex-direction: column;
        }

        &__content-image {
            width: 100%;
            float: none;
        }

        &__content-title {
            order: -2;
        } 

        &__line-text--content {
            order: -1;
        }
    }
}

@media (max-width: 479px) {

    .service {
        padding-top: 70px;

        &__banner-title {
            font-size: 11vw;
        }

        &__line-text--banner {
            margin-top: 20px;
            font-size: 4.9vw;
        }

        &__form {
            padding: 20px;
        }

        &__services-text {
            font-size: 4.4vw;
        }

        &__services-button {
            height: 38px;
            padding: 0 15px;
            font-size: 3vw;
        }

        &__services-card-wrapper {
            display: flex;
            align-items: center;
        }

        &__services-card-title {
            min-height: 45px;
            margin-top: 0;
            padding-left: 15px;
            font-size: 17px;
        }

        &__services-card-image {
            width: 65px;
            height: 65px;
        }

        &__cards-wrapper {
            margin-top: 50px;
        }

        &__cards-title {
            font-size: 38px;
        }

        &__services-card {
            width: 100%;
        }

        &__services-card:not(:first-child) {
            margin-top: 20px;
        }

        &__line-text--content {
            font-size: 4.2vw;
        }

        &__content-text {
            margin-top: 30px;
            font-size: 4.4vw;
        }

        &__content-subtitle {
            font-size: 4.5vw;
        }
    }
}