/*-------- Reputation --------*/

.reputation {
    padding-top: 122px;
    padding-bottom: 214px;

    position: relative;
    text-align: center;
    overflow: hidden;

    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
    }

    &__title {
        margin-top: 122px;
        font-size: 30px;

        z-index: 2;
        position: relative;
    }

    &__circle {
        position: absolute;
        top: 195px;

        animation-name: circle;
        animation-iteration-count: infinite;
        animation-duration: 20s;
        animation-timing-function: linear;

        mix-blend-mode: lighten;
    }

    &__images {
        display: flex;
        align-items: center;
        justify-content: space-between;

        max-width: 1014px;
        margin-top: 100px;
        margin-left: auto;
        margin-right: auto;

        position: relative;
        z-index: 15;
    }
}

@keyframes circle {
    from {
        transform: rotate(0);
    }
    to {
        transform: rotate(360deg);
    }
}

@media (max-width: 1399px) {

    .reputation {
        padding-top: 60px;
    }
}

@media (max-width: 1099px) {

    .reputation {
        padding-bottom: 114px;

        &__title {
            margin-top: 60px;
        }

        &__circle {
            top: 239px;
            width: 37%;
        }

        &__images {
            justify-content: center;
        }

        &__image {
            width: 21%;
        }
    }
}

@media (max-width: 619px) {

    .reputation {
        padding-top: 40px;

        &__title {
            margin-top: 30px;
            font-size: 20px;
        }

        &__images {
            margin-top: 60px;
        }

        &__circle {
            top: 79px;
            width: 60%;
        }
    }
}

@media (max-width: 479px) {

    .reputation {
        padding-bottom: 40px;

        &__title {
            font-size: 18px;
        }

        &__circle {
            width: 78%;
            top: 169px;
        }

        &__images {
            flex-wrap: wrap;
            margin-top: 20px;
        }

        &__image {
            width: 50%;
        }
    }
}